import React from 'react'

export const Login = () => {
    return (
        <div>
            <div id="single-wrapper">
        <form action="#" class="frm-single">
            <div class="inside">
                <div class="title"><img src="assets/images/tac-logo.png" alt=""/></div>
                {/* <!-- /.title --> */}
                <div class="frm-title margin-top-20"><strong>Admin Login</strong></div>
                {/* <!-- /.frm-title --> */}
                <div class="frm-input"><input type="text" placeholder="Username" class="frm-inp"/><i class="fa fa-user frm-ico"></i></div>
                {/* <!-- /.frm-input --> */}
                <div class="frm-input"><input type="password" placeholder="Password" class="frm-inp"/><i class="fa fa-lock frm-ico"></i></div>
                {/* <!-- /.frm-input --> */}
                <div class="clearfix margin-bottom-20">
                    <div class="pull-right"><a href="page-recoverpw.html" class="a-link"><i class="fa fa-unlock-alt"></i>Forgot password?</a></div>
                    {/* <!-- /.pull-right --> */}
                </div>
                {/* <!-- /.clearfix --> */}
                <button type="submit" class="frm-submit">Login<i class="fa fa-arrow-circle-right"></i></button>

                {/* <!-- /.row --> */}
                <a href="page-register.html" class="a-link"><i class="fa fa-key"></i>New to TAC Aligners? Register.</a>

                {/* <!-- /.footer --> */}
            </div>
            {/* <!-- .inside --> */}
        </form>
        {/* <!-- /.frm-single --> */}
    </div>
        </div>
    )
}
